import React, { PureComponent, Fragment } from 'react';
import { Form, Button, Select, Input, Divider } from 'antd';
import FormatterLocale from 'components/FormatterLocale';
import StepFormStore from './formStore';
import styles from './form.module.scss';

const StepForm = (props) => {
  const handleSubmit = (e) => {
    e.preventDefault();
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        StepFormStore.setValue(values);
        StepFormStore.nextStep();
      }
    });
  }
  const { getFieldDecorator } = props.form;
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 16,
        offset: 8,
      },
    }
  }
  return (
    <Fragment>
      <Form {...formItemLayout} onSubmit={handleSubmit}>
        <Form.Item label={<FormatterLocale id="step1.title" defaultMessage="" />}>
          {getFieldDecorator('title', {
            initialValue: '',
            rules: [
              { required: true, message: '!' }
            ]
          })(
            <Input
              placeholder=""
              autoComplete="off"
            />
          )}
        </Form.Item>
        <Form.Item label={<FormatterLocale id="step1.event" defaultMessage="" />}>
          {getFieldDecorator('detail', {
            initialValue: '',
            rules: [
              { required: true, message: '' }
            ]
          })(
            <Input
              placeholder="~"
              autoComplete="off"
            />
          )}
        </Form.Item>
        <Form.Item label={<FormatterLocale id="step1.tellWho" defaultMessage="" />}>
          {getFieldDecorator('user', {
            initialValue: 'noOne',
            rules: [
              { required: true, message: '' }
            ]
          })(
            <Select placeholder="">
              <Select.Option value="me"></Select.Option>
              <Select.Option value="noOne"></Select.Option>
            </Select>
          )}
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit"><FormatterLocale id="button.nextStep" defaultMessage="" /></Button>
        </Form.Item>
      </Form>
    </Fragment>
  );
}

class Step1 extends PureComponent {
  render() {
    const FormContainer = Form.create({ name: 'Step1' })(StepForm);
    return (
      <div>
        <div className={styles.step}><FormContainer /></div>
        <Divider style={{ margin: '40px 0 24px' }} />
        <div className={styles.descript}>
          <h3><FormatterLocale id="step1.attention" defaultMessage="" /></h3>
          <h4><FormatterLocale id="step1.divider" defaultMessage="" /></h4>
          <p><FormatterLocale id="step1.nothing" defaultMessage="" /></p>
        </div>
      </div>
    )
  }
}

export default Step1;